@import url(<link href="https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,900&display=swap" rel="stylesheet">
);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  
}


#root > div > div > nav{
    display: grid;
    font-family: 'Roboto', sans-serif;
    grid-template-columns: 1fr auto;
    padding: 0px 140px;
}

#root > div > div > nav > a{
    font-family: 'Roboto', sans-serif;
    font-size: 32px;
    line-height: 38px;
    text-transform: uppercase;
    color: #361c32;
    display: flex;
}

#basic-navbar-nav{
    justify-self: end;
}

#basic-navbar-nav > div > a{
    border-top: 8px solid transparent;
    font-size: 32px;
    line-height: 38px;
    color: #361c32;
    font-family: 'Roboto', sans-serif;
    padding: 56px 24px;
    -webkit-transition: 0.1s ease-in-out;
    transition: 0.1s ease-in-out;
}

#basic-navbar-nav > div > a:hover{
    border-top: 8px solid #f55951;
}

/* Media querry */

@media screen and (max-width: 1200px) {
    #basic-navbar-nav > div > a:nth-child(n){
        font-size: 22px;
        padding: 36px 14px;
    }
    
    #root > div > div > nav{
        padding: 0px 100px;
    }

    
}

@media screen and (max-width: 992px) {
    #root > div > div > nav > a {
        font-size: 22px;
    }
    #basic-navbar-nav > div > a:nth-child(n){
        font-size: 18px;
    }
    
}

@media screen and (max-width: 768px) {

}
.component__align__right{
    display: grid;
    grid-template-columns: 1fr 1.1fr;
    grid-template-rows: 885px;
    grid-gap: 0px 32px;
    padding: 0px 0px 0px 140px;
    overflow: hidden;
}

.component__align__right_content_left{
    align-self: center;
    color: #361c32;
    font-size: 64px;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    line-height: 1.22;
}

.component__align__right_content_right{
    background-image: url(/made-by-humans/static/media/wcap.c3735f01.jpg);
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
}

/* Media querry */



@media screen and (max-width: 1200px) {
    .component__align__right{
        padding: 0px 0px 0px 100px;
        grid-template-rows: 685px;
    }

}
@media screen and (max-width: 992px) {
    .component__align__right_content_left{
        font-size: 50px;
    }
}
@media screen and (max-width: 768px) {
    .component__align__right_content_left {
        font-size: 38px;
    }
    
}
.component__align__center{
    display: grid;
    font-family: 'Roboto', sans-serif;
    font-size: 32px;
    grid-template-columns: 1fr 2fr;
    grid-gap: 0px 32px;
    padding: 0px 140px;
    margin-top: 148px;
    overflow: hidden;
}

.component__align__center_title{
    color: #f55951;
    font-size: 56px;
}

.component__align__center_paragraphs{
    display: grid;
    grid-row-gap: 50px;
}
.component__align__center_paragraphs > p{
    line-height: 1.5;
}

@media screen and (max-width: 320px) {
    .component__align__center{
        margin-top: 90px;
    }
    .component__align__center_title{
        color: #f55951;
        font-size: 5.73vw;
        text-align: center;
    }
    .component__align__center_paragraphs{
        padding: 0px 1em;
        font-size: 3.27vw;
    }

    #root > div > div.component__align__center > div > p:nth-child(n){
        text-align: center;
    }
}



/* CSS Anim */

#root > div > div:nth-child(3) > div.component__align__center_paragraphs > div:nth-child(1){
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-transition: ease-in;
    transition: ease-in;
}

#root > div > div:nth-child(3) > div.component__align__center_paragraphs > div:nth-child(2){
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
    -webkit-transition: ease-in;
    transition: ease-in;

}

#root > div > div:nth-child(3) > div.component__align__center_paragraphs > div:nth-child(3){
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-transition: ease-in;
    transition: ease-in;

}


/* Media querry */


@media screen and (max-width: 1200px) {

}
  
@media screen and (max-width: 992px) {
    .component__align__center{
        grid-template-columns: 1fr;
        grid-template-rows: repeat(2, auto);
    }
    .component__align__center_title.animated.fadeInDown{
       justify-self: center;
    }
    .component__align__center_paragraphs{
        /* grid-area: content; */
        font-size: 22px;
        grid-template-columns: repeat(3, 1fr);
    }
    .component__align__center_paragraphs > div:nth-child(n){
        padding-right: 18px;
    }
}
@media screen and (max-width: 768px) {
    .component__align__center{
        grid-template-columns: 1fr;
        grid-template-rows: repeat(2, auto);
    }
    .component__align__center_title.animated.fadeInDown{
       justify-self: center;
    }
    .component__align__center_paragraphs{
        display: grid;
        font-size: 22px;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(3, auto);
        text-align: center;
    }
    .component__align__center_paragraphs > div:nth-child(n){
        padding-right: 0px;
    }
}
.grid__img{
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(2, 463px);
    /* grid-template-areas: 
    "img1 img1 img2 img2 img2"
    "img1 img1 . img3 img3"; */
    grid-gap: 32px 23px;
    padding: 0px 140px;
    margin-top: 163px;
    overflow: hidden;
  }

  .grid__img_style{
    background-size: cover;
    background-repeat: no-repeat;
  }

  .grid__img-img1{
    background-image: url(/made-by-humans/static/media/hands.1bafb472.jpg);
    background-position: center top;
