    background-position: center top;
    grid-column-start: 8;
    grid-column-end: 13;
    grid-row-start: 2;
    grid-row-end: 3;
    /* grid-area: img3; */
  }

  
/* Media querry */

@media screen and (max-width: 1200px) {
  .grid__img{
    grid-template-rows: repeat(2, 263px);
  }
}

