.component__align__center{
    display: grid;
    font-family: 'Roboto', sans-serif;
    font-size: 32px;
    grid-template-columns: 1fr 2fr;
    grid-gap: 0px 32px;
    padding: 0px 140px;
    margin-top: 148px;
    overflow: hidden;
}

.component__align__center_title{
    color: #f55951;
    font-size: 56px;
}

.component__align__center_paragraphs{
    display: grid;
    grid-row-gap: 50px;
}
.component__align__center_paragraphs > p{
    line-height: 1.5;
}

@media screen and (max-width: 320px) {
    .component__align__center{
        margin-top: 90px;
    }
    .component__align__center_title{
        color: #f55951;
        font-size: 5.73vw;
        text-align: center;
    }
    .component__align__center_paragraphs{
        padding: 0px 1em;
        font-size: 3.27vw;
    }

    #root > div > div.component__align__center > div > p:nth-child(n){
        text-align: center;
    }
}



/* CSS Anim */

#root > div > div:nth-child(3) > div.component__align__center_paragraphs > div:nth-child(1){
    animation-duration: 0.5s;
    transition: ease-in;
}

#root > div > div:nth-child(3) > div.component__align__center_paragraphs > div:nth-child(2){
    animation-duration: 0.8s;
    transition: ease-in;

}

#root > div > div:nth-child(3) > div.component__align__center_paragraphs > div:nth-child(3){
    animation-duration: 1s;
    transition: ease-in;

}


/* Media querry */


@media screen and (max-width: 1200px) {

}
  
@media screen and (max-width: 992px) {
    .component__align__center{
        grid-template-columns: 1fr;
        grid-template-rows: repeat(2, auto);
    }
    .component__align__center_title.animated.fadeInDown{
       justify-self: center;
    }
    .component__align__center_paragraphs{
        /* grid-area: content; */
        font-size: 22px;
        grid-template-columns: repeat(3, 1fr);
    }
    .component__align__center_paragraphs > div:nth-child(n){
        padding-right: 18px;
    }
}
@media screen and (max-width: 768px) {
    .component__align__center{
        grid-template-columns: 1fr;
        grid-template-rows: repeat(2, auto);
    }
    .component__align__center_title.animated.fadeInDown{
       justify-self: center;
    }
    .component__align__center_paragraphs{
        display: grid;
        font-size: 22px;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(3, auto);
        text-align: center;
    }
    .component__align__center_paragraphs > div:nth-child(n){
        padding-right: 0px;
    }
}