.component__align__right{
    display: grid;
    grid-template-columns: 1fr 1.1fr;
    grid-template-rows: 885px;
    grid-gap: 0px 32px;
    padding: 0px 0px 0px 140px;
    overflow: hidden;
}

.component__align__right_content_left{
    align-self: center;
    color: #361c32;
    font-size: 64px;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    line-height: 1.22;
}

.component__align__right_content_right{
    background-image: url(../Images/wcap.jpg);
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
}

/* Media querry */



@media screen and (max-width: 1200px) {
    .component__align__right{
        padding: 0px 0px 0px 100px;
        grid-template-rows: 685px;
    }

}
@media screen and (max-width: 992px) {
    .component__align__right_content_left{
        font-size: 50px;
    }
}
@media screen and (max-width: 768px) {
    .component__align__right_content_left {
        font-size: 38px;
    }
    
}