
#root > div > div > nav{
    display: grid;
    font-family: 'Roboto', sans-serif;
    grid-template-columns: 1fr auto;
    padding: 0px 140px;
}

#root > div > div > nav > a{
    font-family: 'Roboto', sans-serif;
    font-size: 32px;
    line-height: 38px;
    text-transform: uppercase;
    color: #361c32;
    display: flex;
}

#basic-navbar-nav{
    justify-self: end;
}

#basic-navbar-nav > div > a{
    border-top: 8px solid transparent;
    font-size: 32px;
    line-height: 38px;
    color: #361c32;
    font-family: 'Roboto', sans-serif;
    padding: 56px 24px;
    transition: 0.1s ease-in-out;
}

#basic-navbar-nav > div > a:hover{
    border-top: 8px solid #f55951;
}

/* Media querry */

@media screen and (max-width: 1200px) {
    #basic-navbar-nav > div > a:nth-child(n){
        font-size: 22px;
        padding: 36px 14px;
    }
    
    #root > div > div > nav{
        padding: 0px 100px;
    }

    
}

@media screen and (max-width: 992px) {
    #root > div > div > nav > a {
        font-size: 22px;
    }
    #basic-navbar-nav > div > a:nth-child(n){
        font-size: 18px;
    }
    
}

